import React from 'react';
import DataStore from './dataStore';
import Typewriter from 'typewriter-effect';

const Home = () => {

    const experienceDiv = () => {
        return DataStore.experiences.map(exp => {
            return (
                <div className="single-exp">
                    <div className="title">{exp.title}</div>
                </div>
            )
        })
    }

    return (
        <div className="main-container">
            <div id="myTopnav" class="navigation-bar">
                <a href="#/projects" >Projects</a>
                <a href="#/contact">Contact</a>
            </div>
            <div className="banner">
                <div className="salutation"> Hey there! This is</div>
                <div className="primary-text">
                    Prajwal Mathad
                </div>
                <div className="secondary-text">
                    <span>I am&nbsp;</span>
                    <Typewriter
                        options={{
                            strings: DataStore.Iam,
                            autoStart: true,
                            loop: true,
                        }}
                    />
                </div>


            </div>
            <div className="secondary-container">
                <div className="exp-container section-container">
                    <div className="section-header">Experience </div>
                    {experienceDiv()}
                </div>
                <div className="exp-container section-container">
                    <div className="section-header">Education </div>
                </div>
            </div>
        </div>
    )
}

export default Home;